.rf-logo--sm {
  font-size: 0.7875rem;
}

.rf-logo--sm::before {
  width: 2.0625rem;
  height: 0.75rem;
  margin-bottom: 0.25rem;
  background-size: 2.0625rem 0.84375rem, 2.0625rem 0.75rem, 0;
  background-position: 0 -0.04688rem, 0 0, 0 0;
}

:root[data-rf-mourning] .rf-logo--sm::before {
  background-size: 2.0625rem 0.84375rem, 0, 2.0625rem 0.75rem;
}

.rf-logo--sm::after {
  min-width: 1.96875rem;
  background-size: 3.9375rem 2.8125rem;
  background-position: 0 calc(100% + 1.40625rem);
  padding-top: 1.65625rem;
}

:root[data-rf-theme="dark" i] .rf-logo--sm::after {
  background-position: -1.96875rem 100%;
}

.rf-logo--lg {
  font-size: 1.3125rem;
}

.rf-logo--lg::before {
  width: 3.4375rem;
  height: 1.25rem;
  margin-bottom: 0.41667rem;
  background-size: 3.4375rem 1.40625rem, 3.4375rem 1.25rem, 0;
  background-position: 0 -0.07812rem, 0 0, 0 0;
}

:root[data-rf-mourning] .rf-logo--lg::before {
  background-size: 3.4375rem 1.40625rem, 0, 3.4375rem 1.25rem;
}

.rf-logo--lg::after {
  min-width: 3.28125rem;
  background-size: 6.5625rem 4.6875rem;
  background-position: 0 calc(100% + 2.34375rem);
  padding-top: 2.76042rem;
}

:root[data-rf-theme="dark" i] .rf-logo--lg::after {
  background-position: -3.28125rem 100%;
}

/* ------------------------------------ *\
  PAGINATION
\* ------------------------------------ */
.rf-pagination {
  --is-list: false;
  --is-link: false;
}

.rf-pagination__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 -2px;
}

.rf-pagination__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 0.75rem;
  margin: 2px 2px 0.5rem;
  min-width: 2rem;
  height: 2rem;
}

.rf-pagination__item:not(.rf-pagination__item--first):not(.rf-pagination__item--prev)::before, .rf-pagination__item:not(.rf-pagination__item--next):not(.rf-pagination__item--last)::after {
  content: none;
  display: none;
  margin: 0;
}

.rf-pagination__item--from-md {
  display: none;
}

@media (min-width: 48em) {
  .rf-pagination__item--from-md {
    display: flex;
  }
}

.rf-pagination__item--active {
  border-radius: 1rem;
  pointer-events: none;
}

.rf-pagination__item--first + .rf-pagination__item:not(.rf-pagination__item--prev),
.rf-pagination__item--prev + .rf-pagination__item, .rf-pagination__item--next,
.rf-pagination__item:not(.rf-pagination__item--next) + .rf-pagination__item--last {
  margin-left: 0.75rem;
}

.rf-pagination__item--first, .rf-pagination__item--prev, .rf-pagination__item--next, .rf-pagination__item--last {
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.rf-pagination__item--first::before {
  font-size:  1.5rem;
  content:  "";
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  z-index: 1;
  pointer-events: none;
}

.rf-pagination__item--prev::before {
  font-size:  1.5rem;
  content:  "";
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  z-index: 1;
  pointer-events: none;
}

.rf-pagination__item--next::after {
  font-size:  1.5rem;
  content:  "";
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  pointer-events: none;
}

.rf-pagination__item--last::after {
  font-size:  1.5rem;
  content:  "";
  display: block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0;
  pointer-events: none;
}

.rf-pagination__item--first .rf-pagination__label,
.rf-pagination__item--prev .rf-pagination__label,
.rf-pagination__item--next .rf-pagination__label,
.rf-pagination__item--last .rf-pagination__label {
  display: none;
}

@media (min-width: 48em) {
  .rf-pagination__item--first .rf-pagination__label,
  .rf-pagination__item--prev .rf-pagination__label,
  .rf-pagination__item--next .rf-pagination__label,
  .rf-pagination__item--last .rf-pagination__label {
    display: inline;
  }
}

.rf-pagination__item--first .rf-pagination__label,
.rf-pagination__item--prev .rf-pagination__label {
  padding: 0 0.5rem 0 0.25rem;
}

.rf-pagination__item--next .rf-pagination__label,
.rf-pagination__item--last .rf-pagination__label {
  padding: 0 0.25rem 0 0.5rem;
  display: none;
}

@media (min-width: 48em) {
  .rf-pagination__item--next .rf-pagination__label,
  .rf-pagination__item--last .rf-pagination__label {
    display: inline;
  }
}

.rf-pagination__item--first .rf-pagination__link,
.rf-pagination__item--prev .rf-pagination__link {
  margin-left: -1.75rem;
  padding-left: 1.75rem;
  margin-right: -0.25rem;
  padding-right: 0.25rem;
}

.rf-pagination__item--next .rf-pagination__link,
.rf-pagination__item--last .rf-pagination__link {
  margin-right: -1.75rem;
  padding-right: 1.75rem;
  margin-left: -0.25rem;
  padding-left: 0.25rem;
}

.rf-pagination__item--disabled .rf-pagination__link {
  pointer-events: none;
}

.rf-pagination__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 0.75rem;
  margin: 0 -0.75rem;
  min-width: 2rem;
  height: 2rem;
  box-shadow: none;
  border-radius: 1rem;
  /**
    &:focus {
      outline: none !important;
      &::after {
        content:'';
        position: absolute;
        top: -4px;
        right: -4px;
        bottom: -4px;
        left: -4px;
        border-radius: calc(2rem + 8px);
        @include scheme-element-border-color(focus, true, null, $COLOR solid 2px);
      }
    }
     */
}

.rf-pagination__item {
  color: var(--g800);
}

.rf-pagination__item--active {
  background-color: var(--bf500);
  color: var(--w-bf500);
}

@media (hover: hover) and (pointer: fine) {
  a.rf-pagination__item--active[href]:hover, .rf-pagination__item--active:not(a):not([disabled]):hover, .rf-pagination__item--active:not(a):not(:disabled):hover {
    background-image: linear-gradient(0deg, var(--color-hover), var(--color-hover));
  }
  a.rf-pagination__item--active[href]:active, .rf-pagination__item--active:not(a):not([disabled]):active, .rf-pagination__item--active:not(a):not(:disabled):active {
    background-image: linear-gradient(0deg, var(--color-active), var(--color-active));
  }
}

:root:not([data-rf-theme="dark" i]) .rf-pagination__item--active {
  --color-hover: rgba(0, 0, 246, 0.5);
  --color-active: rgba(91, 91, 255, 0.5);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-light"] .rf-pagination__item--active,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-light-"] .rf-pagination__item--active {
  --color-hover: rgba(0, 0, 246, 0.5);
  --color-active: rgba(91, 91, 255, 0.5);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-dark"] .rf-pagination__item--active,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-dark-"] .rf-pagination__item--active {
  --color-hover: rgba(138, 138, 204, 0.5);
  --color-active: rgba(84, 84, 180, 0.5);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-soft"] .rf-pagination__item--active,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-soft-"] .rf-pagination__item--active {
  --color-hover: rgba(108, 108, 108, 0.5);
  --color-active: rgba(161, 161, 161, 0.5);
}

:root[data-rf-theme="dark" i] .rf-pagination__item--active {
  --color-hover: rgba(138, 138, 204, 0.5);
  --color-active: rgba(84, 84, 180, 0.5);
}

.rf-pagination__item--disabled {
  color: var(--g600-g500);
}

.rf-pagination__link {
  background-color: var(--t-plain);
}

@media (hover: hover) and (pointer: fine) {
  a.rf-pagination__link[href]:hover, .rf-pagination__link:not(a):not([disabled]):hover, .rf-pagination__link:not(a):not(:disabled):hover {
    background-image: linear-gradient(0deg, var(--color-hover), var(--color-hover));
  }
  a.rf-pagination__link[href]:active, .rf-pagination__link:not(a):not([disabled]):active, .rf-pagination__link:not(a):not(:disabled):active {
    background-image: linear-gradient(0deg, var(--color-active), var(--color-active));
  }
}

:root:not([data-rf-theme="dark" i]) .rf-pagination__link {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-light"] .rf-pagination__link,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-light-"] .rf-pagination__link {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-dark"] .rf-pagination__link,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-dark-"] .rf-pagination__link {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root:not([data-rf-theme="dark" i]) [class*="rf-scheme-soft"] .rf-pagination__link,
:root:not([data-rf-theme="dark" i]) [class*="--scheme-soft-"] .rf-pagination__link {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root[data-rf-theme="dark" i] .rf-pagination__link {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

